* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

html {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  background-color: #123;
}

body {
  margin: 0;
}

.flex-separator {
  flex: 1;
}

.start {
  display: flex;
  margin: 0px auto;
  // max-width: 920px;
  align-items: center;
  justify-content: center;
}

.title-header {
  display: block;
  text-decoration: none;
  text-align: center;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
  position: relative;
  padding: 50px 0px 70px;
  flex: 1;

  .logo {
    padding: 30px 20px 0px;
    font-size: 0;

    img {
      width: 140px;
    }
  }

  .name {
    margin: 0px auto;
    padding: 20px 20px;
    max-width: 640px;

    h1,
    h2,
    p {
      margin: 0;
      padding: 0;
    }

    h1 {
      color: #234;
      font-size: 2.2em;
      font-family: Lato, Helvetica, sans-serif;

      a {
        text-decoration: none;
        color: inherit;
      }
    }

    h2 {
      font-size: 1em;
      font-weight: normal;
      font-family: Lato, Helvetica, sans-serif;
      color: #678;
      padding-bottom: 2em;
    }

    p {
      color: #456;
      font-size: 1.2em;
      padding-bottom: 1em;
    }
  }

  .links {
    a {
      display: inline-block;
      padding: 15px;
      text-align: center;
      text-decoration: none;
      flex: 1;
      color: #555;
      font-size: 1em;

      &:hover {
        color: #08f;
      }

      &:first-child {
        border-left: none;
      }

      &:last-child {
        border-right: none;
      }
    }
  }
}

.container {
  width: 100%;
  overflow: hidden;
}

section {
  padding: 20px;
}

.about-me {
  background-color: #f6f8ff; // #ab0c43;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  margin: 0px 0px 40px;
  padding: 30px 20px;
  overflow: hidden;

  .somehow-comet {
    z-index: 0;
    position: absolute;
    top: 0px;
    left: -480px;
    opacity: 0.1;
  }

  &-inner {
    display: flex;
    padding: 20px;
    max-width: 960px;
    margin: 0px auto;
    justify-content: flex-end;

    h2 {
      font-family: Lato, Helvetica, sans-serif;
      font-size: 2em;
      margin: 0px;
      color: #123;
    }

    p {
      margin: 0;
      padding: 0.5em 0;
      color: #234;
      font-size: 1.2em;
    }

    .stuffs {
      max-width: 640px;
      position: relative;
    }

    .buttons {
      a {
        display: inline-block;
        padding: 3px 0px;
        text-decoration: none;
        border-bottom: 2px solid #a2b7ff;
        color: darken(#a2b7ff, 15%);
        transition: all 0.25s linear;

        &:hover {
          color: darken(#a2b7ff, 30%);
          border-color: darken(#a2b7ff, 25%);
        }
      }
    }
  }
}

.open-source {
  background: linear-gradient(
    45deg,
    #0044dd 0%,
    #2989d8 50%,
    #2989d8 50%,
    #0044ff 100%
  );
  box-shadow: 0 0 5px #123;
  padding: 40px 20px;
  margin: 20px 10px;
  border-radius: 100% 200px;
  text-align: center;

  h2 {
    color: #fff;
    font-size: 2em;
    font-family: Lato, Helvetica, sans-serif;
    text-align: center;
  }

  .show-more-less-btn {
    display: inline-block;
    margin: 15px auto;
    padding: 0px 15px;
    border-radius: 15px;
    line-height: 26px;
    color: #fff;
    text-align: center;
    cursor: pointer;
    border: 2px solid #fff;

    &:hover {
      background-color: #fff;
      color: #123;
    }
  }
}

.projects {
  display: flex;
  align-items: stretch;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-grow: 3;
  text-align: center;
  border-radius: 2px;

  &--hiding {
    overflow: hidden;
    height: 170px;
  }
}

.project {
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column;
  margin: 10px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  min-width: 240px;
  transition: all 0.15s ease-out;
  top: 0px;

  &:hover {
    top: -4px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
  }

  .about {
    flex: 1;
    justify-content: center;
    align-items: center;
    padding: 20px;
    min-height: 100px;

    h3 {
      font-family: Lato, Roboto, sans-serif;
    }

    h3,
    p {
      margin: 0px;
    }
  }

  .links {
    display: flex;
    justify-content: center;
    align-items: stretch;
    border-top: 1px solid #eee;

    a {
      display: block;
      flex: 1;
      padding: 15px;
      text-decoration: none;
      color: inherit;

      border-right: 1px solid #eee;

      &:last-child {
        border-right: none;
      }
    }

    a:hover {
      color: #08f;
    }
  }
}

.page {
  padding: 50px 0px;
  background-color: #fff;
}

.document {
  img {
    display: block;
    max-width: 100%;
    max-height: 520px;
    font-size: 0;
    margin: 0px auto;
    padding: 0px;
  }

  blockquote {
    display: block;
    padding: 30px;
    margin: 0px;

    .quote {
      display: block;
      max-width: 780px;
      margin: 0px auto;
      font-size: 2.2em;
      text-align: center;
      font-family: Lato;
      font-weight: bold;
    }

    .attribution {
      display: block;
      color: #555;
      text-align: center;
      padding: 10px;

      &:before {
        content: '–';
        padding-right: 5px;
      }
    }
  }

  .content {
    padding: 30px 20px;
    max-width: 720px;
    margin: 0px auto;

    h1 {
      margin: 0px;
      font-family: Lato, Helvetica, sans-serif;
      font-size: 2.4em;
      color: #012;
    }

    h2 {
      margin: 0px;
      font-family: Lato, Helvetica, sans-serif;
      font-size: 1.8em;
      color: #012;
    }

    h3 {
      margin: 0px;
      font-family: Lato, Helvetica, sans-serif;
      font-size: 1.5em;
      color: #012;
    }

    p {
      color: #012;
      font-size: 1.3em;
      line-height: 1.4em;
      margin: 0px;
      padding-bottom: 1.5em;

      &:last-child {
        padding-bottom: 0;
      }
    }

    a {
      color: inherit;
      text-decoration: underline;

      &:hover {
        color: #08f;
      }
    }

    ul,
    ol {
      margin: 0;
      padding: 0 0 1em 20px;
    }

    li {
      padding: 0 0 0.75em 0;
      font-size: 1.3em;
      line-height: 1.4em;

      p {
        font-size: 1em;
      }
    }

    time {
      display: block;
      color: #444;
      font-size: 1.1em;
      line-height: 1.4em;
    }

    pre {
      margin: 0px;
      padding-bottom: 2em;
      font-size: 1em;
    }

    pre > code {
      display: block;
      background-color: #eee;
      padding: 0.5em;
    }

    code {
      font-family: Monaco, monospace;
      border-radius: 3px;
    }

    hr {
      border: none;
      border-top: 1px solid #ddd;
      margin: 3em 0;
    }
  }
}

.talks {
  background-color: #fff;
  box-shadow: 0 0 5px #123;
  border: 4px dashed rgba(0, 0, 0, 0.2);
  transform: rotate(1deg);
  padding: 40px 40px 50px;
  margin: 40px -20px;
  position: relative;
  overflow: hidden;

  .slide-funk {
    z-index: -1;
    position: absolute;
    top: -40px;
    right: 0px;
    opacity: 0.1;
  }

  &-inner {
    transform: rotate(-1deg);

    & > p {
      display: flex;
      align-items: center;
      justify-content: center;

      color: #345;

      .offer {
        display: inline-block;
        background-color: #ffb700;
        color: #fff;
        padding: 3px 5px;
        margin: 0px 5px;
        font-size: 0.7em;
        // text-transform: uppercase;
        border-radius: 2px;
        vertical-align: middle;
        white-space: nowrap;
      }
    }
  }

  h2 {
    color: #111;
    font-size: 2em;
    font-family: Lato, Helvetica, sans-serif;
    text-align: center;
  }

  .talk-list {
    list-style-type: none;
    margin: 0px auto;
    max-width: 640px;
    padding: 20px 0px;
    text-align: center;

    .talk {
      padding: 12px 0px;

      h3 {
        margin: 0px;
        padding: 8px 5px;

        a {
          font-size: 1.5em;
          font-family: Lato, Helvetica, sans-serif;
          text-decoration: none;
          color: #123;
          border-bottom: 2px solid #ddd;
          transition: all 0.25s linear;

          &:hover {
            color: #000;
            border-bottom-color: #aaa;
          }

          &:after {
            content: '↗';
            font-size: 0.8em;
            margin-left: 3px;
            opacity: 0.8;
          }
        }
      }

      p {
        margin: 0px;
        color: #444;
      }
    }
  }
}

.texts {
  background-color: #f1f7fe;
  border-bottom: 1px solid #d2dde9;
  margin: -100px 0px 0px;
  padding: 100px 0 30px;
  overflow: hidden;

  &-inner {
    position: relative;
    max-width: 640px;
    margin: 0px auto;
  }

  .book-funk {
    position: absolute;
    bottom: -80px;
    right: 100%;
    opacity: 0.1;
    padding-right: 25px;
  }

  h2 {
    color: #111;
    font-size: 2em;
    font-family: Lato, Helvetica, sans-serif;
    padding: 20px;
    margin: 0px auto;
  }

  ul {
    padding: 25px 20px;
    margin: 0 auto;
    list-style: none;
    justify-content: center;
    display: flex;
    flex-direction: column;
  }

  li {
    display: inline-block;
    padding: 10px 0px;
  }

  h4 {
    font-size: 1.4em;
    font-family: Lato, Helvetica, sans-serif;
    margin: 0px;

    a {
      display: inline-block;
      text-decoration: none;
      color: inherit;
      border-bottom: 2px solid rgba(0, 0, 0, 0.1);
      margin-bottom: 5px;
      transition: 0.1s linear all;

      &:hover {
        color: #000;
        border-bottom: 2px solid #000;
      }
    }
  }

  p {
    font-size: 1em;
    margin: 0px;
  }
}

.comics {
  background-color: #fff;

  &-title {
    display: block;
    width: 100%;
    max-width: 560px;
    padding: 30px 10px 10px;
    margin: 0px auto;
  }

  &-action {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 120px;
  }

  &-button {
    display: block;

    img.plain {
      display: block;
      width: 150px;
    }
    img.hover {
      display: none;
      width: 180px;
    }

    &:hover {
      img.plain {
        display: none;
      }
      img.hover {
        display: block;
      }
    }
  }
}

.contact {
  position: relative;
  max-width: 640px;
  margin: 0px auto;
  padding: 50px 20px 25px 20px;

  .cavebob {
    position: absolute;
    z-index: -1;
    right: 0;
    bottom: 0;
    width: 420px;
    max-width: 80%;
    opacity: 0.2;
  }

  @media (max-width: 480px) {
    font-size: 0.7em;
  }

  h2 {
    font-size: 2em;
    font-family: Lato, Helvetica, sans-serif;
    color: #fff;
    text-shadow: 0 0 5px #234;

    a {
      text-decoration: none;
      color: inherit;
    }
  }

  dt {
    display: flex;
    align-items: center;
    color: #f4f4f4;
    font-size: 1.2em;

    .prefer {
      display: inline-block;
      background-color: #32ad64;
      color: #fff;
      padding: 3px 5px;
      margin: 0px 5px;
      font-size: 0.7em;
      // text-transform: uppercase;
      border-radius: 2px;
      vertical-align: middle;
    }
  }

  dd {
    margin: 0px;
    padding-bottom: 1em;

    a {
      display: inline-block;
      text-decoration: none;
      text-shadow: 0 0 5px #123;
      color: #08f;
      font-size: 1.5em;
      font-family: Lato, Helvetica, sans-serif;

      padding: 3px 0px;
      border-bottom: 2px solid #048;

      transition: all 0.25s linear;

      &:after {
        content: '↗';
        font-size: 0.8em;
        margin-left: 3px;
        opacity: 0.8;
      }

      &:hover {
        color: #0af;
        border-color: #0af;
      }
    }
  }
}

.big-quote {
  padding: 50px 20px;
  text-align: center;
  position: relative;

  h2 {
    color: #fff;
    text-decoration: 0 0 5px #234;
    margin: 0px;
    padding: 10px 0px;
    font-size: 1.6em;
    font-family: Lato, Helvetica, sans-serif;
  }

  a {
    color: inherit;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}

.copyright {
  padding: 20px;
  margin-top: 0px;

  p {
    color: #fff;
    font-size: 0.8em;
    text-align: center;
    opacity: 0.5;
  }
}
